import React, { Component } from 'react';
import { Link } from 'react-router'
import pureRender from "pure-render-decorator"

@pureRender
export default class ChnGroup extends Component {
  constructor(props){
    super(props)
    this.state={
      tags:[
        {
          cid:'news_all',
          name:''
        },
				{
          cid:'news_fashion',
          name:''
        },
        {
          cid:'video',
          name:''
        },
        {
          cid:'news_society',
          name:''
        },
        {
          cid:'news_entertainment',
          name:''
        },
        {
          cid:'news_military',
          name:''
        },
        {
          cid:'news_tech',
          name:''
        },
        {
          cid:'news_car',
          name:''
        },
        {
          cid:'news_sports',
          name:''
        },
        {
          cid:'news_finance',
          name:''
        },
				{
          cid:'news_world',
          name:''
        },

      ]
    }
  }
  render() {
    const { tags } = this.state
    return (
      <div className="channel-tags-wrap">
        <div className="channel-tags-list">
          {
            tags.map((item,index)=>{
              return <Link to={`/news/${item.cid}`} activeClassName="active" key={`navitem_${index}`}>{item.name}</Link>
            })
          }
        </div>
      </div>
    );
  }
}



